/* Layout and component styles layered on top of Figma tokens compiled in globals.css */

/* Base page */
html { font-size: var(--font-size); }
body {
  margin: 0;
  background: #ffffff;  /* pure white base */
  color: var(--foreground);
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  position: relative;
  z-index: 0;
}

/* TOP gradient (blue at top fading to white) */
body::before {
  content: "";
  position: fixed;
  inset: 0 0 auto 0;
  height: 420px; /* adjust as needed */
  background: linear-gradient(180deg, #EAF9FF 0%, #FFFFFF 100%);
  pointer-events: none;
  z-index: -9999; /* push far behind all content */
}

/* Utilities */
.container { max-width: 1120px; margin: 0 auto; padding: 0 24px; }
.muted { color: var(--muted-foreground); }

/* Cards & buttons */
.card { background: var(--card); color: var(--card-foreground); border-radius: 12px; border: 1px solid var(--border); box-shadow: 0 1px 2px rgba(2, 6, 23, 0.06), 0 4px 12px rgba(2, 6, 23, 0.04); }
.btn { display: inline-flex; align-items: center; justify-content: center; border-radius: 8px; padding: 12px 16px; font-weight: 600; border: 1px solid transparent; cursor: pointer; transition: background .2s, box-shadow .2s; }
.btn-primary { 
    background: linear-gradient(90deg, #00ADEF 0%, #6BD9FF 100%); 
    color: #ffffff; 
    border: none;
  }
  .btn-primary:hover { 
    background: linear-gradient(90deg, #006AAF 0%, #3AADD7 100%); 
    box-shadow: 0 8px 20px rgba(0,77,140,0.3); 
  }
/* Secondary button style for white buttons */
.btn-secondary { 
    background: #ffffff; 
    color: #004C8C; 
    border: 1px solid rgba(0, 173, 239, 0.35);
    box-shadow: 0 1px 2px rgba(2, 6, 23, 0.04);
  }
  .btn-secondary:hover { 
    background: #EAF9FF; /* Light blue tint on hover */
    box-shadow: 0 4px 12px rgba(0, 173, 239, 0.15); /* Enhanced shadow on hover */
  }

/* Navigation */
.nav { background: #ffffff; border-bottom: 1px solid rgba(2, 6, 23, 0.06); }
.nav .container { display: flex; align-items: center; justify-content: space-between; padding-top: 14px; padding-bottom: 14px; }
.nav-left { display: flex; align-items: center; gap: 10px; color: var(--foreground); }
.brand-name { font-weight: 800; font-size: 20px; letter-spacing: -0.01em; color: var(--foreground); }
.nav-right { display: flex; align-items: center; gap: 24px; color: var(--muted-foreground); }
.nav-link { color: var(--muted-foreground); text-decoration: none; font-weight: 600; padding: 6px 8px; }
.nav-link:hover { color: var(--foreground); }
.nav-link.signin { border: 1px solid rgba(0, 173, 239, 0.35); padding: 6px 10px; border-radius: 999px; color: #004C8C; background: #ffffff; box-shadow: 0 1px 2px rgba(2, 6, 23, 0.04); }
.nav-link.signin:hover { 
  background: #00ADEF;
  color: #ffffff;
  border-color: #00ADEF;
}

/* Hero */
.hero { padding: 72px 0; }
.hero-title { font-size: 48px; line-height: 1.2; margin: 0 0 16px; font-weight: 800; letter-spacing: -0.01em; color: #004C8C; }
.hero-subtitle { font-size: 18px; color: var(--muted-foreground); margin: 0 0 32px; }
.metrics { display: grid; grid-template-columns: repeat(3, minmax(0, 1fr)); gap: 16px; margin-top: 24px; }
.metric { padding: 16px; }
.metric h3 { margin: 0 0 4px; font-size: 20px; color: #004C8C; }
.metric p { margin: 0; font-size: 14px; color: var(--muted-foreground); }

/* Features */
.features { display: grid; grid-template-columns: repeat(5, minmax(0, 1fr)); gap: 20px; margin-top: 32px; }
.feature-card { padding: 16px; }
.feature-title { margin: 8px 0; font-size: 18px; color: #004C8C; }
.feature-desc { margin: 0; color: var(--muted-foreground); font-size: 14px; }

/* Chat page */
.chat-page { height: 100vh; display: grid; grid-template-rows: auto 1fr auto; }
.chat-messages { padding: 16px; overflow-y: auto; }
.chat-input-dock { padding: 12px; border-top: 1px solid var(--border); background: var(--card); }
.bubble { max-width: 72%; padding: 12px 14px; border-radius: 16px; margin: 6px 0; box-shadow: 0 1px 2px rgba(2, 6, 23, 0.06); }
.bubble-user { background: var(--primary); color: var(--primary-foreground); border-bottom-right-radius: 4px; margin-left: auto; }
.bubble-assistant { background: #ffffff; color: var(--foreground); border: 1px solid var(--border); border-bottom-left-radius: 4px; }
.bubble-meta { margin-top: 6px; font-size: 12px; color: var(--muted-foreground); }
.typing { display: inline-flex; gap: 4px; padding: 6px 8px; border-radius: 12px; background: var(--secondary); color: var(--secondary-foreground); font-size: 12px; align-items: center; }
.dot { width: 6px; height: 6px; border-radius: 999px; background: currentColor; opacity: .6; animation: blink 1s infinite ease-in-out; }
.dot:nth-child(2) { animation-delay: .15s; }
.dot:nth-child(3) { animation-delay: .3s; }
@keyframes blink { 0%, 80%, 100% { opacity: .2 } 40% { opacity: 1 } }

/* Chat mockup */
.mockup { width: 420px; border-radius: 16px; overflow: hidden; box-shadow: 0 8px 24px rgba(0, 77, 140, 0.18); background: #fff; border: 1px solid var(--border); }
.mockup-header { height: 54px; background: linear-gradient(90deg, #0a84d0 0%, #1fb7ff 100%); }
.mockup-body { padding: 12px; }
.mockup-row { display: flex; align-items: flex-start; gap: 8px; margin: 8px 0; }
.mockup-row.right { justify-content: flex-end; }
.mockup-avatar { width: 16px; height: 16px; border-radius: 999px; background: #0a84d0; margin-top: 6px; }
.mockup-bubble { background: #ffffff; border: 1px solid var(--border); border-radius: 12px; padding: 10px 12px; max-width: 75%; font-size: 13px; color: var(--foreground); }
.mockup-bubble.user { background: #0a84d0; color: #fff; border-color: transparent; }
.mockup-input { margin-top: 10px; border: 1px solid var(--border); border-radius: 12px; padding: 8px 10px; color: var(--muted-foreground); font-size: 12px; }

/* CTA */
.cta { margin: 40px auto; max-width: 980px; border-radius: 24px; padding: 36px 24px; color: #fff; background: linear-gradient(90deg, #0a84d0 0%, #1fb7ff 100%); box-shadow: 0 10px 30px rgba(10,132,208,0.25); text-align: center; }
.cta h4 { margin: 0 0 8px; font-size: 22px; font-weight: 700; }
.cta p { margin: 0 0 20px; opacity: 0.95; }
.cta, .features, .footer, .hero {
  position: relative;
  z-index: 1;
}


/* PRE-FOOTER gradient (white footer, fade upward into light blue) */
.footer::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: -200px;
  height: 200px;
  background: linear-gradient(0deg, #EAF9FF 0%, #FFFFFF 100%);
  pointer-events: none;
  z-index: -9999; /* also far behind all elements */
}

/* Footer */
.footer {
  padding: 48px 0;
  border-top: 1px solid var(--border);
  background: #ffffff;   /* ensure footer is solid white */
  position: relative;    /* anchor ::before */
  z-index: 0;
}

.footer-grid { display: grid; grid-template-columns: 2fr 1fr 1fr 1fr; gap: 24px; }
.footer h5 { margin: 0 0 12px; font-size: 14px; color: var(--foreground); }
.footer a { color: var(--muted-foreground); text-decoration: none; font-size: 14px; display: block; margin: 6px 0; }
.footer a:hover { color: var(--foreground); }

/* Responsive */
@media (max-width: 900px) {
    .features { grid-template-columns: 1fr; }
    .metrics { grid-template-columns: 1fr; }
    .hero { padding: 48px 0; }
    .hero-title { font-size: 36px; }
    /* Add this: */
    .hero .container[style*="grid"] {
      grid-template-columns: 1fr !important;
    }
  }

  /* Chat header */
.chat-header {
  background: #ffffff;
  border-bottom: 1px solid var(--border);
  padding: 16px 0;
}

.chat-header-content {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.back-button {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  background: none;
  border: none;
  color: var(--muted-foreground);
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  padding: 0;
}

.back-button:hover {
  color: var(--foreground);
}

.chat-header-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.chat-header-icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

.chat-title {
  margin: 0;
  font-size: 20px;
  font-weight: 700;
  color: #004C8C;
}

.chat-status {
  margin: 0;
  font-size: 14px;
  color: var(--muted-foreground);
}

/* Message rows with avatars */
.message-row {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin: 12px 0;
}

.message-row-user {
  justify-content: flex-end;
}

.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-top: 4px;
}

.avatar-assistant {
  background: #E6F7FF;
}

/* Enhanced markdown styling for travel assistant */
.bubble-assistant h1 {
  font-size: 18px !important;
  font-weight: 700 !important;
  margin: 12px 0 8px 0 !important;
  color: #004C8C !important;
  line-height: 1.3 !important;
}

.bubble-assistant h2 {
  font-size: 16px !important;
  font-weight: 600 !important;
  margin: 10px 0 6px 0 !important;
  color: #004C8C !important;
  line-height: 1.3 !important;
}

.bubble-assistant h3 {
  font-size: 14px !important;
  font-weight: 600 !important;
  margin: 8px 0 4px 0 !important;
  color: #004C8C !important;
  line-height: 1.3 !important;
}

.bubble-assistant table {
  width: 100% !important;
  border-collapse: collapse !important;
  font-size: 14px !important;
  border: 1px solid var(--border) !important;
  border-radius: 8px !important;
  overflow: hidden !important;
  margin: 8px 0 !important;
}

.bubble-assistant table tr:nth-child(even) {
  background-color: #f8fafc !important;
}

.bubble-assistant table tr:not(:last-child) {
  border-bottom: 1px solid var(--border) !important;
}

.bubble-assistant table td {
  padding: 8px 12px !important;
  text-align: left !important;
  border-right: 1px solid var(--border) !important;
}

.bubble-assistant table td:last-child {
  border-right: none !important;
}

.bubble-assistant table tr:first-child td {
  font-weight: 600 !important;
  color: #004C8C !important;
  background-color: #EAF9FF !important;
}

/* Quick replies */
.quick-replies {
  display: flex;
  gap: 12px;
  margin-bottom: 12px;
  flex-wrap: wrap;
}

.quick-reply-btn {
  background: #EAF9FF;
  color: #004C8C;
  border: 1px solid rgba(0, 173, 239, 0.25);
  border-radius: 999px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.quick-reply-btn:hover {
  background: #D5F3FF;
  border-color: rgba(0, 173, 239, 0.4);
}


